<template>
  <plan :dietPlan="thePlan" :type="achievementType" />
</template>

<script>
import Plan from "@/components/Plan.vue";

export default {
  name: "DietPlan",
  props: {
    achievementType: {
      type: String,
      required: true,
    },
  },
  components: { Plan },
  computed: {
    timeInterval() {
      return {
        wakingUp: "Upon waking app",
        breakfast: "Breakfast",
        lunch: "Lunch",
        dinner: "Dinner",
        bedtime: "Bed time",
      };
    },
    thePlan() {
      if (this.achievementType === "gain") {
        return this.gainPlans;
      }
      return this.lossPlans;
    },
    lossPlans() {
      const t = this.timeInterval;
      return {
        [t.wakingUp]: ["ü•õ 1-2 glass luke warm water"],
        [t.breakfast]: ["üçµ 1 bowl of oats"],
        [t.lunch]: ["üçå fruit"],
        [t.dinner]: ["ü•ó one bowl of salad"],
      };
    },
    gainPlans() {
      const t = this.timeInterval;
      return {
        [t.breakfast]: [
          "ü•ö 2 boiled eggs",
          "üçû 2 slice of multi-grain bread with butter",
          "ü•£ bowl of oatmeal",
          "üçä orange juice",
        ],
        [t.lunch]: ["üçñ meat", "üçó chicken or pork", "üçö cups of rice"],
        [t.dinner]: [
          "üç£ fish",
          "üçõ stir fried veggies with baked potatoes",
          "üçò brown rice",
        ],
        [t.bedtime]: ["ü•õ milk"],
      };
    },
  },
};
</script>

